nav {
  height: 50px;
  background: #212121;
}

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style: none;

  .menu_link {
    font-weight: 300;
    font-size: 15px;
    color: #FFFFFF;
    text-decoration: none;
  }

  .menu_item {
    border-right: 2px dotted #000000;
    padding-right: 40px;
  }
}

.subheader {
  position: absolute;
  width: 100%;
  height: 84px;
  background-color: rgba(0, 0, 0, 0.6);

  .subheader_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px;
  }

  .logo_text {
    font-weight: 100;
    font-size: 13px;
    color: #FFFFFF;
    opacity: 0.5;
    margin-top: 6px;
  }
}

.subheader_call {
  margin-top: 18px;
  font-weight: 300;
  font-size: 12px;
  line-height: 14ppx;
  color: #FFFFFF;
  text-align: right;
  opacity: 0.5;
}

.subheader_number {
  display: block;
  margin-top: 8px;
  font-weight: 400;
  font-size: 22px;
  color: #FFFFFF;
  text-transform: uppercase;
  text-align: right;
  line-height: 12px;
}

.subheader_btn {
  border-radius: 8px;
  border-bottom-style: none;
  border: 0px;
  margin-top: 19px;
  width: 172px;
  height: 46px;
  background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
  box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
  border-radius: 4px;
  font-weight: 400;
  font-size: 14px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  @include btn_hover;
}

.promo {
  height: 800px;
  background: url(../img/bg/uber_bg.png);
  padding: 150px 0;
}

.promo_text {
  margin: 0;
  font-weight: 900;
  font-size: 60px;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  line-height: 60px;
}

.promo_subheader {
  margin: 0;
  margin-top: 22px;
  font-weight: 700;
  font-size: 36px;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  line-height: 36px;
}

.text_subheader {
  margin-top: 16px;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  display: flex;
  align-items: center;
  text-align: center;
  color: #ffffff;
}

.promo_btn {
  display: block;
  margin: 115px auto 0 auto;
  border-radius: 8px;
  border-bottom-style: none;
  border: 0px;
  width: 259px;
  height: 63px;
  background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
  border-radius: 4px;
  font-weight: 400;
  font-size: 18px;
  text-transform: uppercase;
  color: #FFFFFF;
  @include btn_hover;
}

.benefits {}

.label {
  display: block;
  margin: 0 auto;
  margin-top: 18px;
  width: 115px;
  height: 27px;
  background: #000000;
  font-weight: 400;
  font-size: 11px;
  text-transform: uppercase;
  color: #FFFFFF;
  text-align: center;
  padding: 7px;
}

.titel {
  display: block;
  margin-top: 32px;
  font-weight: 700;
  font-size: 38px;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: #222222;
}

.wrapper_oll {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.wrapper1 {
  .round {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 116px;
    height: 116px;
    border-radius: 300px;
    background: #1EACC7;
  }
  padding: 16px;
  display: flex;
  align-items: center;
}
.wrapper_text {
  font-weight: 300;
  font-size: 15px;
  line-height: 21px;
  color: #202020;
  width: 371px;
  height: 150px;
  margin-left: 25px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.wrapper_span {
  font-weight: 400;
  font-size: 17px;
  line-height: 21px;
  color: #1EACC7;
  margin-bottom: -14px;
}

.mobile {
  padding: 48px 0 80px 0;
  max-height:864px ;
  background-image: url(../img/bg/mobile_bg.png);
  .row {
    margin-top: 30px;
  }
  &_item {
    position: relative;
    width: 320px;
    height: 280px;
    margin: 0 auto;
    margin-bottom: 19px;
    padding-top: 92px;
    &_1 {
      background-image: url(../img/app/app1.png);
      background-repeat: no-repeat; background-size: cover; background-position: center;
    }

    &_2 {
      background-image: url(../img/app/app2.png);
      background-repeat: no-repeat; background-size: cover; background-position: center;
    }

    &_3 {
      background-image: url(../img/app/app3.png);
      background-repeat: no-repeat; background-size: cover; background-position: center;
    }

    &_4 {
      background-image: url(../img/app/app4.png);
      background-repeat: no-repeat; background-size: cover; background-position: center;
    }

    &_5 {
      background-image: url(../img/app/app5.png);
      background-repeat: no-repeat; background-size: cover; background-position: center;
    }

    &_6 {
      background-image: url(../img/app/app6.png);
      background-repeat: no-repeat; background-size: cover; background-position: center;
    }
    &_subtitle {
      font-weight: 400;
      font-size: 24px;
      line-height: 31px;
      text-transform: uppercase;
      text-align: center;
      color: #FFFFFF;
    }
    &_plus {
      display: flex;
      justify-content:center;
      position: absolute;
      bottom: 35px;
      left: 153px;
      width: 26px;
      height: 26px;
      background: #000000;
      margin: 0 auto;
      border-radius:100%;
      padding: 6px;
      cursor: pointer;


    }
  }
  .titel {
    margin-top: 32px;
  }
  .subtitle {
    font-weight: 400;
    font-size: 17px;
    line-height: 21px;
    text-align: center;
    color: #222222;
  }
}

.car {
  position: relative;
  padding-top:59px;
  .label {
    display: block;
    margin: 0 auto;
    background:#ffffff;
    width: 112px;
    height: 24px;
    font-weight: 400;
    font-size: 11px;
    line-height: 11px;
    text-transform: uppercase;
    color: #070716;
  }
  .titel {
    font-weight: 700;
    font-size: 38px;
    line-height: 48px;
    text-transform: uppercase;
    color: #FFFFFF;
  }
  .subtitle {
    font-weight: 400;
    font-size: 17px;
    line-height: 21px;
    text-align: center;
    color: #FFFFFF;
    margin-top: 10px;
  }

}

.black {
  position: relative;
  background-color: #070716;
  min-height: 502px;
  padding: 57px;
  .img_car {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: -230px;
  }
}

.white {
  background-image: url(../img/car_roud.png);
  min-height: 397px;
  .car_descr {
    display: block;
    margin: 0 auto;
    padding-top: 250px;
    font-weight: 300;
    font-size: 17px;
    line-height: 24px;
    text-align: center;
    color: #222222;

  }

}

.require {
  padding: 66px 0 87px 0;
  &_block {
    margin: 33px 0;
  }
  &nbm {
    margin-bottom:0 px;
  }
  &_title {
    font-weight: 400;
    font-size: 22px;
    line-height: 24px;
    color: #1CA8C3;
  }
  &_list {
    list-style: none;
    padding-left: 50px;
    margin-top: 22px;

    li {
      position: relative;
      font-weight: 300;
      font-size: 17px;
      line-height: 24px;
      color: #222222;
      margin-bottom: 10px;
      &:before {
        content: '';
        display:block;
        position: absolute;
        left: -32px;
        top: 3px;
        width: 21px;
        height: 20px;
        background-image: url(../icons/done/checkblue.svg);
        background-repeat: no-repeat; background-size: cover; background-position: center;
      }
    }
  }

  &_descr {
    font-weight: 300;
    font-size: 17px;
    line-height: 24px;
    color: #1CA8C3;
  }
  .warning {
    .require_title {
      color: $second-color;
    }
    .require_list {
      li {
        &:before {
          background-image: url(../icons/done/checkred.svg);
          background-repeat: no-repeat; background-size: cover; background-position: center;
        }
      }
    }
  }


}

.finish {
  background-image: url(../img/bg/world_bg.png);
  position: relative;
  padding-top: 59px;

  .label {
    display: block;
    margin: 0 auto;
    background: #ffffff;
    width: 112px;
    height: 24px;
    font-weight: 400;
    font-size: 11px;
    line-height: 11px;
    text-transform: uppercase;
    color: #070716;
  }

  .titel {
    font-weight: 700;
    font-size: 38px;
    line-height: 48px;
    text-transform: uppercase;
    color: #FFFFFF;
  }
  .subtitle {
    font-weight: 400;
    font-size: 17px;
    line-height: 21px;
    color: #FFFFFF;
    text-align: center;
    margin-top: 10px;
  }
  &_button {
    display: flex;
    justify-content:center;
    margin-top:456px;
    text-decoration: none;
    font-weight: 400;
    font-size: 15px;
    line-height: 68px;
    color: #1FBAD6;
    text-transform: uppercase;
    &:hover {
      text-decoration:none;
      color: #1FBAD6;
    }


  }
}

